body {
   background-color: #1E1E1E;
 }
 
 .AppMami{
  background-color: #1E1E1E;
 } 

 .App {
   text-align: center;
   max-width: 1100px;
   margin: 0 auto;
   background-color: #212026;

 }

 .sideEchat{
     display: flex;
 }
 
 
 header {
   background-color: #1E1E1E;
   height: 10vh;
   min-height: 50px;
   color: white;
   position: fixed;
   width: 100%;
   top: 0;
   display: flex;
   align-items: center;
   justify-content: space-between;
   z-index: 99;
   padding: 10px;
   box-sizing: border-box;
 }
 
 .sidebar{
  width: 90%;
 }
 
 .App section {
   display: flex;
   flex-direction: column;
   justify-content: center;
   min-height: 95vh;
   width: 1010px;

 }
 
 main {
   padding: 10px;
   height: 80vh;
   margin: 10vh 0 10vh;
   overflow-y: scroll;
   display: flex;
   flex-direction: column;
   width: 1000px;
 }
 
 main::-webkit-scrollbar {
   width: 0.25rem;
 }
 
 main::-webkit-scrollbar-track {
   background: #1e1e24;
 }
 
 main::-webkit-scrollbar-thumb {
   background: #6649b8;
 }
 
 form {
   height: 10vh;
   position: fixed;
   bottom: 0;
   background-color: rgb(24, 23, 23);
   width: 100%;
   max-width: 1000px;
   display: flex;
   font-size: 1.5rem;
 }
 
 form button {
   width: 30%;
   background-color: rgb(56, 56, 143);
 }
 
 input {
   line-height: 1.5;
   width: 100%;
   font-size: 1.5rem;
   background: rgb(58, 58, 58);
   color: white;
   outline: none;
   border: none;
   padding: 0 10px;
 }
 
 button {
   background-color: #282c34; 
   border: none;
   color: white;
   padding: 15px 32px;
   text-align: center;
   text-decoration: none;
   display: inline-block;
   cursor: pointer;
   font-size: 1.25rem;
 }
 
 button:disabled {
   opacity: 0.5;
   cursor: not-allowed;
 }
 
 .sign-in {
   color: #282c34;
   background: white;
   max-width: 900px;
   margin: 0 auto;
 }
 
 ul,
 li {
   text-align: left;
   list-style: none;
 }
 
 p {
   max-width: 500px;
   margin-bottom: 12px;
   line-height: 24px;
   padding: 10px 20px;
   border-radius: 25px;
   position: relative;
   color: white;
   text-align: center;
 }
 
 .message {
   display: flex;
   align-items: center;
 }
 
 .sent {
   flex-direction: row-reverse;
 }
 
 .sent p {
   color: white;
   background: #0b93f6;
   align-self: flex-end;
 }
 .received p {
   background: #e5e5ea;
   color: black;
 }
 
 img {
   width: 40px;
   height: 40px;
   border-radius: 50%;
   margin: 2px 5px;
 }
